import React, { useState, useEffect } from "react";
import NewsTab from "../PageElements/NewsTab";
import WatchlistGrid from "../PageElements/WatchlistGrid";
import WatchlistTab from "../PageElements/WatchlistTab";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import CustomTab from "../PageElements/CustomTab";

const WatchlistPage = (props) => {
  const [selectedWatchlist, setSelectedWatchlist] = useState(1);
  const [watchlists, setWatchLists] = useState([
    {
      name: "watchlist!",
      symbols: [
        "TSLA",
        "AMZN",
        "ILMN",
        "AAPL",
        "NFLX",
        "CHGG",
        "SPOT",
        "MSFT",
        "AGTC",
      ],
    },
    { name: "Tech Stocks!!", symbols: ["TSLA", "AMZN"] },
  ]);

  const getTabs = () => watchlists.map((list) => list.name);

  const getTabPanels = () => {
    return watchlists.map((list) => {
      return <WatchlistGrid stockSymbols={list.symbols} />;
    });
  };

  return (
    <Container maxWidth="lg" disableGutters={true}>
      <Grid item lg>
        <CustomTab tabs={getTabs() || []}>{getTabPanels() || []}</CustomTab>
      </Grid>

      <Grid container direction="row" wrap="nowrap">
        <Grid item zeroMinWidth xs></Grid>
        <Grid item zeroMinWidth xs>
          <NewsTab
            header
            stockSymbols={watchlists[selectedWatchlist].symbols || []}
          ></NewsTab>
        </Grid>
      </Grid>
    </Container>
  );
};

export default WatchlistPage;
